import React, { useState } from "react";
import { PROJECT_LIST } from "../../Constant";
import { Card, Col, Row } from "react-bootstrap";

const Projects=()=>{
    return (
        <>
         <h2>PROJECTS</h2> <hr />
         <Row xs={1} md={2} className="g-4">
            {PROJECT_LIST.map((item)=>{
                return <Col><Project project={item}/></Col>
            })}
         </Row>
        
        </>
    )
}

const Project=({project})=>{

    const [isMouseHover, setIsMouseHover] = useState(false);
    const mouseOver =()=>{
        setIsMouseHover(true)
    }
    const mouseLeave =()=>{
        setIsMouseHover(false)
    }

    let project_desc  = isMouseHover?project.desc:project.desc.slice(0,50)
    const variant = "light"
    return(<>
    <Card  bg={variant.toLowerCase()}
          key={project._id}
          text={variant.toLowerCase() === 'light' ? 'dark' : 'white'}
          style={{ width: '38rem' }}
          className="mb-5 text-center"
          onMouseOver={mouseOver}
          onMouseLeave={mouseLeave}>
      <Card.Body>
      <Card.Title>{project.name}</Card.Title>
      <br />
      <iframe title="vimeo-player" src={project.embeded_photo_video[0].link} width="600" height="300" frameBorder="0"    allowFullScreen></iframe>
      <br />
       
        <Card.Text>
        
        <b>Description: </b> {project_desc}
        </Card.Text>
     
      </Card.Body>
      <Card.Footer className="text-muted"><b>Type: </b>{project.type}</Card.Footer>
    </Card>
    </>)
}
export default Projects
